.blog {
    h2 {
        font-size: 3.4rem;
        margin: 0;
        color: $blanco;
        font-weight: bold;
    }
    background-color: lighten($negro, 7);
}

.imagen {
    height: auto;
    img {
        margin-left: 1rem;
        height: 30rem;
    }
}

.entrada-blog {
    gap: 3rem;
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

.contenedor-entrada {
    background-color: #323444;
    border-radius: 2rem;
    padding: 2rem;
    margin-bottom: 1rem;
    @include tablet {
        margin: 1rem 7rem;
        width: auto;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.texto-entrada {
    a {
        color: $blanco;
        display: block;
        margin-top: 2rem;
        &:hover {
            color: violet;
        }
        @include tablet {
            margin-top: 0;
        }
    }
    h4 {
        margin: 0;
        line-height: 1.4;
        &::after {
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }
    p {
        color: $blanco;
    }
    span {
        color: violet;
    }
}

.contenedor-entrada {
    transition: transform 0.2s;
}

.contenedor-entrada:hover {
    transform: scale(1.02);
}