.no-webp .myBook {
    background-image: url('../img/ContactBackground.png');
}

.webp .myBook {
    background-image: url('../img/ContactBackground.webp');
}

.myBook {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    padding-top: 2rem;
    h2 {
        margin: 0;
        color: $blanco;
    }
    padding-bottom: 3rem;
}

body {
    margin-left: 0;
}

.contenedorLibro {
    @include tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
    }
    .descriptionBook {
        @media (max-width: 768px) and (max-height: 916px) {
            padding: 20rem 1.5rem 0;
        }
        p {
            color: $blanco;
            justify-content: center;
        }
    }
}

.flipcard {
    perspective: 50%;
    @media (max-width: 360px) {
        padding-top: 1.5rem;
        margin-right: 2rem;
    }
    @include telefono {
        padding-bottom: 10rem;
    }
    @include tablet {
        padding: 0;
    }
}

.flipcard-inner {
    position: relative;
    width: 35%;
    height: 30%;
    @include desktopXL {
        width: 30%;
    }
    top: 50%;
    left: 40%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0,
    0,
    0,
    0.2);
}

.flipcard:hover .flipcard-inner {
    transform: rotateY(180deg);
}

.flipcard-front,
.flipcard-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
}

.flipcard-front {
    background-color: #bbb;
    color: black;
}

.flipcard-back {
    background-color: #2980b9;
    color: white;
    transform: rotateY(180deg);
}

.contenedor-btn {
    display: flex;
    justify-content: flex-end;
    margin: 0 2rem;
    padding-bottom: 2rem;
    .btn-comprar {
        display: flex;
        background-color: violet;
        width: 20rem;
        height: auto;
        padding: 1rem 2rem;
        justify-content: center;
        align-items: center;
        white-space: nowrap;
        // margin-top: 1rem;
        a {
            font-weight: bold;
            color: $blanco;
            font-size: 1.8rem;
        }
        p {
            font-weight: $bold;
            color: $blanco;
            font-size: 1.8rem;
            margin: 0 .5rem;
        }
        img {
            width: 3rem;
            height: 3rem;
            margin: 0;
        }
        &:hover {
            background-color: darken(violet, 8);
            cursor: pointer;
        }
    }
}

.divAdviser {
    @media (max-width: 770px) {
        display: none;
    }
    position: relative;
    padding-top: 1rem;
}

.adviser {
    position: absolute;
    bottom: 0;
    left: 40%;
    color: white;
    width: 35%;
}