.btn-violet {
    display: flex;
    background-color: $violet;
    width: 10rem;
    height: auto;
    padding: 1rem 7rem;
    align-items: center;
    justify-content: center;
    color: $blanco;
    &:hover {
        background-color: darken($violet, 10);
    }
}

.btn-rojo-block {
    display: flex;
    background-color: rgb(232, 6, 6);
    width: 10rem;
    height: auto;
    padding: 1rem 7rem;
    align-items: center;
    justify-content: center;
    color: $blanco;
    cursor: pointer;
    margin: 0;
    &:hover {
        background-color: darken(rgb(232, 6, 6), 5);
    }
}

.btn-verde-block {
    display: flex;
    background-color: rgb(25, 174, 30);
    width: 10rem;
    height: auto;
    padding: 1rem 7rem;
    align-items: center;
    justify-content: center;
    color: $blanco;
    margin: 0;
    &:hover {
        background-color: darken(rgb(25, 174, 30), 5);
    }
}

.contenedor-btnCrud {
    padding-top: 1rem;
}

.btn-crud {
    cursor: pointer;
}

.botones-crud {
    display: flex;
    flex-direction: column;
    max-width: 20rem;
}

fieldset {
    .boton {
        // height: 5rem;
        // width: 25rem;
        padding: 1rem 3rem;
        margin: 0 0 2rem 0;
    }
}