.tablaEstudios {
    table {
        border-collapse: collapse;
        width: 100%;
        color: white;
    }
    table,
    th,
    td {
        border: 1px solid #383838;
    }
    th,
    td {
        padding: 15px;
        text-align: left;
    }
    ul {
        list-style-type: disc;
        padding-left: 25px;
    }
    td {
        @media (max-width: 375px) {
            font-size: 1rem;
        }
    }
}