h1 {
    color: $blanco;
    text-align: center;
    margin: 0;
}

.header {
    @include desktop {
        // position: fixed;
    }
    width: 100%;
    background-color: lighten($negro, 9);
}

.contenido-header {
    padding: 2rem;
    display: block;
    @include tablet {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.main-nav {
    display: block;
    text-align: center;
    justify-content: center;
    align-items: center;
    transition: visibility 0.1s ease-in-out, opacity 0.2s linear, height 0.5s linear;
    //Mobile Menu
    visibility: hidden;
    opacity: 0;
    height: 0;
    @include tablet {
        opacity: 1;
        visibility: visible;
        display: flex;
        align-items: center;
        place-items: center;
    }
    a {
        display: block;
        color: $blanco;
        text-decoration: none;
        font-size: 1.8rem;
        text-align: center;
        @include tablet {
            display: flex;
            margin-right: 2rem;
            &:last-of-type {
                margin-right: 0;
            }
        }
        p {
            margin: 0;
            color: $blanco;
            font-size: 1.5rem;
        }
    }
}

.enlace {
    padding: 0 5.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &:hover {
        img {
            filter: none;
        }
        p {
            color: $negro;
        }
        background-color: $verde;
    }
    width: 13rem;
    height: auto;
    border-radius: 2rem;
    img {
        filter: invert(1);
        height: 2rem;
        width: 2rem;
    }
}

.contenedorNav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
    }
}

.imageContainer {
    width: 6rem;
    height: 6rem;
    @media (max-width: 768px) {
        margin-bottom: 2rem;
    }
}

//Mobile Menu
.mobile-menu {
    @include tablet {
        display: none;
    }
    img {
        width: 3rem;
        padding-bottom: 1rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}