body {
    background-color: lighten($negro, 7);
}

.home {
    h1 {
        font-size: 3.8rem;
        margin: 0;
        color: $blanco;
        font-weight: bold;
    }
    h2 {
        font-size: 3.4rem;
        margin: 0;
        color: $blanco;
        font-weight: bold;
    }
    h3 {
        font-size: 3rem;
        margin: 0;
        color: $blanco;
        font-weight: bold;
    }
}

.content-information {
    padding: 0 .8rem;
    @include tablet {
        padding: 0;
    }
}

.content-home {
    width: 100%;
    @include tablet {
        display: grid;
        gap: 8rem;
        justify-content: center;
        grid-template-columns: repeat(2, 1fr);
    }
    p {
        color: $blanco;
        margin: 0;
        font-size: 2.4rem;
        justify-content: center;
    }
}

.animation {
    &:hover {
        font-weight: bold;
        // font-size: 2.8rem;
        font-family: $fuente-secundaria;
        color: $violet;
    }
}

.image-home {
    width: 100%;
    border-radius: 3rem;
    @include tablet {
        margin-bottom: 5rem;
        margin-top: 1rem;
        margin-left: 3rem;
    }
}