#imagen {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.lupa {
    position: absolute;
    top: 0;
    right: 0;
    width: 64px;
    height: 64px;
    background-color: rgba(0, 0, 0, 0.75);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: zoom-in;
}

.lupa-visor {
    position: absolute;
    width: 500px;
    height: 500px;
    background-color: rgba(0, 0, 0, 0.75);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    overflow: hidden;
    display: none;
    opacity: 0;
    transition: all 0.2s;
}

#imagen-lupa {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(2);
}

.lupa:hover+.lupa-visor {
    display: block;
    opacity: 1;
}