/** UI **/
h1 {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
}

.header {
  width: 100%;
  background-color: #171717;
}
.contenido-header {
  padding: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .contenido-header {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.main-nav {
  display: block;
  text-align: center;
  justify-content: center;
  align-items: center;
  transition: visibility 0.1s ease-in-out, opacity 0.2s linear, height 0.5s linear;
  visibility: hidden;
  opacity: 0;
  height: 0;
}
@media (min-width: 768px) {
  .main-nav {
    opacity: 1;
    visibility: visible;
    display: flex;
    align-items: center;
    place-items: center;
  }
}
.main-nav a {
  display: block;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.8rem;
  text-align: center;
}
@media (min-width: 768px) {
  .main-nav a {
    display: flex;
    margin-right: 2rem;
  }
  .main-nav a:last-of-type {
    margin-right: 0;
  }
}
.main-nav a p {
  margin: 0;
  color: #FFFFFF;
  font-size: 1.5rem;
}

.enlace {
  padding: 0 5.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 13rem;
  height: auto;
