.card button:hover {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.work-experience input {
  display: none;
}

.container {
  width: 100%;
  height: 100%;
  max-width: 700px;
  max-height: 600px;
  display: flex;
  transform-style: preserve-3d;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.cards {
  position: relative;
  height: 50rem;
  width: 36rem;
  max-height: auto;
  margin-bottom: 2rem;
  perspective: 25rem;
  transform-style: preserve-3d;
}
@media (max-width: 375px) {
  .cards {
    width: 30rem;
    margin-right: 1.4rem;
    perspective: 24rem;
  }
}
@media (max-width: 375px) and (max-height: 480px) {
  .cards {
    margin-left: 1rem;
  }
}
@media (max-width: 768px) {
  .cards {
    perspective: 21rem;
  }
}
@media (min-width: 768px) and (max-width: 868px) and (max-height: 1180px) {
  .cards {
    margin-right: 7rem;
    perspective: 25rem;
  }
}
@media (min-width: 868px) and (max-width: 912px) and (max-height: 1368px) {
  .cards {
    margin-right: 5rem;
  }
}
@media (min-width: 1024px) {
  .cards {
    perspective: 100rem;
    width: 43rem;
  }
}

.cards label {
  position: absolute;
  width: 35rem;
  height: 45rem;
  max-height: 45rem;
  left: 0;
  right: 0;
  margin: auto;
  cursor: pointer;
  transition: transform 0.55s ease;
}
@media (max-width: 375px) {
  .cards label {
    width: 28rem;
    height: 40rem;
  }
}
@media (max-width: 375px) and (max-height: 480px) {
  .cards label {
    height: 34rem;
    width: 27rem;
  }
}
@media (min-width: 1024px) {
  .cards label {
    max-height: 65rem;
    height: 65rem;
  }
}

.cards .card {
  position: relative;
  height: 100%;
  background-color: #323444;
  border-radius: 10px;
  padding: 30px 35px;
}

.card .image {
  display: flex;
  justify-content: space-between;
}

.card .image img {
  border-radius: 5px;
  box-shadow: 15px 15px 40px rgba(0, 0, 0, 0.5);
  margin-top: 23px;
  width: 86%;
  height: 12rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .card .image img {
    height: 22rem;
  }
}

.card .image .dots {
  text-align: center;
}

.dots div {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.dots div:nth-child(1) {
  background-color: var(--current-color1);
}

.card .infos {
  display: block;
  padding-top: 2rem;
}
@media (min-width: 1024px) {
  .card .infos {
    text-align: end;
    padding-top: 5rem;
  }
}

.infos span {
  display: block;
  height: auto;
}

.infos .name {
  font-size: 2rem;
  color: var(--current-color1);
  height: auto;
  margin-bottom: 20px;
  padding-left: 25%;
  transition: all 0.6s ease;
}
@media (min-width: 1024px) {
  .infos .name {
    letter-spacing: 2px;
  }
}
@media (max-width: 375px) and (max-height: 480px) {
  .infos .name {
    margin-bottom: 0;
  }
}

.infos .lorem {
  font-size: 1.4rem;
  color: #ECEAED;
  height: auto;
}
@media (min-width: 768px) {
  .infos .lorem {
    letter-spacing: 3px;
  }
}

.card .btn-contact {
  width: 100%;
  height: 60px;
  margin: 35px auto;
  border-radius: 5px;
  background-color: var(--current-color1);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 25px;
  letter-spacing: 10px;
  font-weight: 700;
  transition: all 0.7s ease;
  border: none;
}

#s1:checked ~ .cards #slide4,
#s2:checked ~ .cards #slide5,
#s3:checked ~ .cards #slide1,
#s4:checked ~ .cards #slide2,
#s5:checked ~ .cards #slide3 {
  box-shadow: 0 15px 35px 0 rgba(0, 0, 0, 0.45);
  transform: translate3d(-70%, 0, -220px);
  --current-color1: #ECEAED;
  --current-color2: #404457;
}

#s1:checked ~ .cards #slide5,
#s2:checked ~ .cards #slide1,
#s3:checked ~ .cards #slide2,
#s4:checked ~ .cards #slide3,
#s5:checked ~ .cards #slide4 {
  box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.45);
  transform: translate3d(-35%, 0, -120px);
  --current-color1: #ECEAED;
  --current-color2: #404457;
}

#s1:checked ~ .cards #slide1,
#s2:checked ~ .cards #slide2,
#s3:checked ~ .cards #slide3,
#s4:checked ~ .cards #slide4,
#s5:checked ~ .cards #slide5 {
  box-shadow: 0 25px 50px 0 rgba(0, 0, 0, 0.5);
  transform: translate3d(0, 0, 0);
  --current-color1: violet;
  --current-color2: #ECEAED;
}

#s1:checked ~ .cards #slide2,
#s2:checked ~ .cards #slide3,
#s3:checked ~ .cards #slide4,
#s4:checked ~ .cards #slide5,
#s5:checked ~ .cards #slide1 {
  box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.45);
  transform: translate3d(35%, 0, -120px);
  --current-color1: #ECEAED;
  --current-color2: #404457;
}

#s1:checked ~ .cards #slide3,
#s2:checked ~ .cards #slide4,
#s3:checked ~ .cards #slide5,
#s4:checked ~ .cards #slide1,
#s5:checked ~ .cards #slide2 {
  box-shadow: 0 15px 35px 0 rgba(0, 0, 0, 0.45);
  transform: translate3d(70%, 0, -220px);
  --current-color1: #ECEAED;
  --current-color2: #404457;
}

