body {
  margin-left: 0;
}

@media (min-width: 768px) {
  .contenedorLibro {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (max-width: 768px) and (max-height: 916px) {
  .contenedorLibro .descriptionBook {
    padding: 20rem 1.5rem 0;
  }
}
.contenedorLibro .descriptionBook p {
  color: #FFFFFF;
  justify-content: center;
}

.flipcard {
  perspective: 50%;
}
@media (max-width: 360px) {
  .flipcard {
    padding-top: 1.5rem;
    margin-right: 2rem;
  }
}
@media (min-width: 480px) {
  .flipcard {
    padding-bottom: 10rem;
  }
}
@media (min-width: 768px) {
  .flipcard {
    padding: 0;
  }
}

.flipcard-inner {
  position: relative;
  width: 35%;
  height: 30%;
  top: 50%;
  left: 40%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
@media (min-width: 1200px) {
  .flipcard-inner {
    width: 30%;
  }
}

.flipcard:hover .flipcard-inner {
  transform: rotateY(180deg);
}

.flipcard-front,
.flipcard-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.flipcard-front {
  background-color: #bbb;
  color: black;
}

.flipcard-back {
  background-color: #2980b9;
  color: white;
  transform: rotateY(180deg);
}

.contenedor-btn {
