  color: #FFFFFF;
  font-size: 1.8rem;
}
.contenedor-btn .btn-comprar p {
  font-weight: 700;
  color: #FFFFFF;
  font-size: 1.8rem;
  margin: 0 0.5rem;
}
.contenedor-btn .btn-comprar img {
  width: 3rem;
  height: 3rem;
  margin: 0;
}
.contenedor-btn .btn-comprar:hover {
  background-color: #e95ee9;
  cursor: pointer;
}

.divAdviser {
