.forms {
    p {
        font-size: 1.4rem;
        color: darken($blanco, 5);
        margin: 2rem 0 0 0;
    }
    legend {
        font-size: 2rem;
        color: darken($blanco, 5);
    }
    label {
        font-weight: $bold;
        text-transform: uppercase;
        color: $blanco;
        display: block;
    }
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: darken($blanco, 7);
        border: 1px solid;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
    textarea {
        height: 20rem;
        resize: none;
    }
}