.card {
    button {
        &:hover {
            cursor: pointer;
        }
    }
}

a {
    text-decoration: none;
}

.work-experience {
    input {
        display: none;
    }
}

.container {
    width: 100%;
    height: 100%;
    max-width: 700px;
    max-height: 600px;
    display: flex;
    transform-style: preserve-3d;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.cards {
    position: relative;
    height: 50rem;
    width: 36rem;
    max-height: auto;
    margin-bottom: 2rem;
    perspective: 25rem;
    @media (max-width: 375px) {
        width: 30rem;
        margin-right: 1.4rem;
        perspective: 24rem;
    }
    @media (max-width: 375px) and (max-height: 480px) {
        margin-left: 1rem
    }
    @media (max-width: 768px) {
        perspective: 21rem;
    }
    @media (min-width: 768px) and (max-width: 868px) and (max-height: 1180px) {
        margin-right: 7rem;
        perspective: 25rem;
    }
    @media (min-width: 868px) and (max-width: 912px) and (max-height: 1368px) {
        margin-right: 5rem;
    }
    @include desktop {
        perspective: 100rem;
        width: 43rem;
    }
    transform-style: preserve-3d;
}

.cards label {
    position: absolute;
    width: 35rem;
    height: 45rem;
    max-height: 45rem;
    @media (max-width: 375px) {
        width: 28rem;
        height: 40rem;
    }
    @media (max-width: 375px) and (max-height: 480px) {
        height: 34rem;
        width: 27rem;
    }
    @include desktop {
        max-height: 65rem;
        height: 65rem;
    }
    left: 0;
    right: 0;
    margin: auto;
    cursor: pointer;
    transition: transform 0.55s ease;
}

.cards .card {
    position: relative;
    height: 100%;
    background-color: #323444;
    border-radius: 10px;
    padding: 30px 35px;
}

.card .image {
    display: flex;
    justify-content: space-between;
}

.card .image img {
    border-radius: 5px;
    box-shadow: 15px 15px 40px rgba(0, 0, 0, 50%);
    margin-top: 23px;
    width: 86%;
    height: 12rem;
    @include desktop {
        height: 22rem;
    }
    object-fit: cover;
}

.card .image .dots {
    text-align: center;
}

.dots div {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-bottom: 10px;
}

.dots div:nth-child(1) {
    background-color: var(--current-color1);
}

.card .infos {
    display: block;
    padding-top: 2rem;
    @include desktop {
        text-align: end;
        padding-top: 5rem;
    }
}

.infos span {
    display: block;
    height: auto;
}

.infos .name {
    font-size: 2rem;
    color: var(--current-color1);
    height: auto;
    @include desktop {
        letter-spacing: 2px;
    }
    @media (max-width: 375px) and (max-height: 480px) {
        margin-bottom: 0;
    }
    margin-bottom: 20px;
    padding-left: 25%;
    transition: all .6s ease;
}

.infos .lorem {
    font-size: 1.4rem;
    color: #ECEAED;
    @include tablet {
        letter-spacing: 3px;
    }
    height: auto;
}

.card .btn-contact {
    width: 100%;
    height: 60px;
    margin: 35px auto;
    border-radius: 5px;
    background-color: var(--current-color1);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 25px;
    letter-spacing: 10px;
    font-weight: 700;
    transition: all .7s ease;
    border: none;
}

#s1:checked~.cards #slide4,
#s2:checked~.cards #slide5,
#s3:checked~.cards #slide1,
#s4:checked~.cards #slide2,
#s5:checked~.cards #slide3 {
    box-shadow: 0 15px 35px 0 rgba(0, 0, 0, 45%);
    transform: translate3d(-70%, 0, -220px);
    --current-color1: #ECEAED;
    --current-color2: #404457;
}

#s1:checked~.cards #slide5,
#s2:checked~.cards #slide1,
#s3:checked~.cards #slide2,
#s4:checked~.cards #slide3,
#s5:checked~.cards #slide4 {
    box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 45%);
    transform: translate3d(-35%, 0, -120px);
    --current-color1: #ECEAED;
    --current-color2: #404457;
}

#s1:checked~.cards #slide1,
#s2:checked~.cards #slide2,
#s3:checked~.cards #slide3,
#s4:checked~.cards #slide4,
#s5:checked~.cards #slide5 {
    box-shadow: 0 25px 50px 0 rgba(0, 0, 0, 50%);
    transform: translate3d(0, 0, 0);
    --current-color1: violet;
    --current-color2: #ECEAED;
}

#s1:checked~.cards #slide2,
#s2:checked~.cards #slide3,
#s3:checked~.cards #slide4,
#s4:checked~.cards #slide5,
#s5:checked~.cards #slide1 {
    box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 45%);
    transform: translate3d(35%, 0, -120px);
    --current-color1: #ECEAED;
    --current-color2: #404457;
}

#s1:checked~.cards #slide3,
#s2:checked~.cards #slide4,
#s3:checked~.cards #slide5,
#s4:checked~.cards #slide1,
#s5:checked~.cards #slide2 {
    box-shadow: 0 15px 35px 0 rgba(0, 0, 0, 45%);
    transform: translate3d(70%, 0, -220px);
    --current-color1: #ECEAED;
    --current-color2: #404457;
}