.no-webp .contact {
    background-image: url('../img/ContactBackground.png');
}

.webp .contact {
    background-image: url('../img/ContactBackground.webp');
}

.contact {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom center;
    padding-top: 2rem;
    h2 {
        margin: 0;
        color: $blanco;
    }
    padding-bottom: 3rem;
}

.btn-send-contact {
    background-color: violet;
    width: 20rem;
    color: $blanco;
    padding: 1rem 3rem;
    border: none;
    text-align: center;
    margin-top: 1rem;
    font-weight: $bold;
    &:hover {
        background-color: darken(violet, 5);
        cursor: pointer;
    }
}

.contactContainer {
    display: flex;
    width: 70%;
    @include tablet {
        width: 50rem;
    }
    height: auto;
    margin: 0 auto;
    background-color: $blanco;
    border-radius: 1rem;
    text-align: center;
    justify-content: center;
    fieldset {
        border: none;
        width: 100%;
    }
}

.contactContainer {
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
}

.contactContainer {
    legend {
        font-size: 2rem;
        color: lighten($negro, 12);
    }
    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }
    textarea {
        height: 20rem;
        resize: none;
    }
}